import React, { useState, useEffect } from 'react';
import { useVoice } from '../hooks/useVoice';

type Transcription = {importance: string, transcript: string};

export const VoiceView: React.FC = () => {
    const voice = useVoice();

    const [isImportant, setIsImportant] = useState<boolean>(false);
    const [finished, setFinished] = useState<boolean>(false);
    const [transcriptions, setTranscriptions] = useState<Transcription[]>([]);
    const [finalTranscript, setFinalTranscript] = useState<string>("");

    useEffect(() => {
        if (finished) setFinalTranscript(JSON.stringify(transcriptions));
    }, [finished, transcriptions]);

    const addPartialTranscript = (transcription: Transcription) => {
        setTranscriptions([
            transcription,
            ...transcriptions
        ]);
    };

    const saveAndResetTranscript = () => {
        if (isImportant) addPartialTranscript({importance: "important", transcript: voice.transcript});
        else addPartialTranscript({importance: "baseline", transcript: voice.transcript});
        voice.resetTranscript();
    }

    const startNewTranscript = (important: boolean) => {
        saveAndResetTranscript();
        setIsImportant(important);
    };

    const finalizeTranscript = () => {
        setFinished(true);
        saveAndResetTranscript();
        setIsImportant(false);
    };

    //prob wanna use useEffect to manually save the transcription every time as well if voiceRemove.transcript is like not in local memory??

    return (
        <>
            <div style={{paddingLeft: "20px", paddingRight: "20px", paddingBottom: "20px"}}>
                <p>Microphone: {voice.listening ? 'on' : 'off'}</p>
                <button className="light-btn" onClick={voice.startListening}>Start</button>
                <button className="light-btn" style={{marginLeft: "20px"}} onClick={() => startNewTranscript(true)}>Important</button>
                <button className="light-btn" style={{marginLeft: "5px"}} onClick={() => startNewTranscript(false)}>Baseline</button>
                <button className="light-btn" style={{marginLeft: "5px"}} onClick={finalizeTranscript}>Finish</button>
                <p>Press the Start button to begin.</p>
                <p style={{color: `${isImportant ? "red" : "blue"}`}}>{voice.transcript}</p>
            </div>
            <div style={{marginTop: "20px"}}>
                {transcriptions.map(transcription =>
                    <p style={{color: `${transcription.importance === "important" ? "red" : "blue"}`}}>{transcription.transcript}</p>
                )}
            </div>
            <p>{finalTranscript}</p>
        </>
    );
};